import React from "react";
import needle from "../images/needle.png";
import odometer from "../images/twap.png";
import {makeStyles} from "@material-ui/core/styles";
import {Box, Typography} from "@material-ui/core";


const useStyles = makeStyles((theme) => ({
    wrapper: {
        position: "relative",
        display: "grid",
        gridTemplateColumns: "1rf",
        width: "100%",
        height: "100%",
    },
    odometer: {
        width: "100%",
        gridColumn: "1/1",
        gridRow: "1",
    },
    needle: {
        width: "75%",
        height: "auto",
        justifySelf:"center",
        alignSelf: "center",
        gridColumn: "1/1",
        gridRow: "1",
    },
    twap: {
        justifySelf:"center",
        alignSelf: "center",
        gridColumn: "1/1",
        gridRow: "1",
        position: "relative",
        zIndex: "10",
        textAlign:"center",
        paddingTop:"140px"
    }
}));

export default function Twap(props){
    const classes = useStyles();

    const range = (currentTwap) => {
        return currentTwap*244/50;
    }

    return (
        <Box className={classes.wrapper}>
            <img className={classes.needle} style={{transform: `rotate(${range(props.twap)+4}deg)`}} src={needle} alt={"Needle"}/>
            <img className={classes.odometer} src={odometer} alt={"odometer"}/>
            <Box className={classes.twap}>
                <Typography variant={"h5"}>
                    Current NITRO Bonus<br/>
                    {props.twap}%
                </Typography>
            </Box>
        </Box>
    );
}
