import React, {useRef, useState} from 'react'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import { Link } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles'
import LamboMoon from '../images/lambomoon.png';
import {IconButton, Typography} from "@material-ui/core";
import MusicNoteIcon from '@material-ui/icons/MusicNote';
import Drawer from "@material-ui/core/Drawer";
import {useMusic} from "./MusicProvider";
import Box from "@material-ui/core/Box";
import MusicPlayer from "./MusicPlayer";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
  navBarWrapper:{
    position: "relative",
    zIndex: "100",
  },
  banner:{
    backgroundColor: theme.palette.primary.main,
    color: theme.palette.primary.contrastText,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    padding: theme.spacing(1)
  },
  Navbar: {
    fontSize: '2em',
    backgroundColor: 'transparent',
    color:"white",
    fontFamily: 'AstroSpace',
    display: "flex",
  },
  NavbarBrand: {
    fontSize: '1.2em',
    marginLeft: theme.spacing(5),
  },
  nav:{
    flexGrow: "1",
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
  },
  logo: {
    height: "50px",
    width: "auto",
    marginRight: theme.spacing(2)
  },
  links: {
    color: "inherit",
    margin: `0 ${theme.spacing(2)}px`,
    "&:hover":{
      color: theme.palette.primary.dark
    },
    "&[disabled]": {
      color: "gray",
      textDecoration: "none"
    }
  }
}))

export default function DegenNavbar(props) {
  const classes = useStyles();
  const [musicDrawerOpen, setMusicDrawerOpen] = useState(false);

  const handleMusicClick = () => {
    setMusicDrawerOpen(true);
  };

  const handleMusicClose = () => {
    setMusicDrawerOpen(false);
  }

  return (
      <Box className={classes.navBarWrapper}>
        <Box className={classes.banner}>
          <Typography style={{ marginRight: 8}}>
            Pre-sale start December 11 @ 3PM EST
          </Typography>
          <Button color="secondary" variant={"outlined"} component={Link} to="/presale">
            Visit presale
          </Button>
        </Box>
        <Navbar className={classes.Navbar} expand="lg" variant="dark">
          <Link to="/" className={classes.links}>
            <Navbar.Brand
                className={classes.NavbarBrand}
            >
              <img className={classes.logo} src={LamboMoon} alt={"logo"}/>
              LAMBO
            </Navbar.Brand>
          </Link>
          <Navbar.Toggle aria-controls="navbar-nav" />
          <Navbar.Collapse id="navbar-nav">
            <Nav className={classes.nav}>
              <Link to="/nitro" className={classes.links}>
                NITRO
              </Link>
              <Link to="/dealership" className={classes.links}>
                DEALERSHIP
              </Link>
              <Link to="#" disabled={true} className={classes.links}>
                SWAP
              </Link>
              <Link to="/about" className={classes.links}>
                ABOUT
              </Link>
              <IconButton aria-controls={"music-menu"} onClick={handleMusicClick} className={classes.links}>
                <MusicNoteIcon/>
              </IconButton>
              <Drawer anchor={"right"} open={musicDrawerOpen} onClose={handleMusicClose}>
                <Box p={3}>
                  <Typography component={"h2"} variant={"h4"}>Lambo beats</Typography>
                  <MusicPlayer/>
                </Box>
              </Drawer>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      </Box>
  )
}
