import React from 'react';
import {makeStyles} from "@material-ui/core/styles";
import {Box} from "@material-ui/core";
import { ReactComponent as Lambo } from "../images/lambo-icon-optimized.svg";

const useStyles = makeStyles((theme) => ({
    wrapper: {
        display: 'grid',
        gridTemplateColumns: "repeat(12,1fr)",
        position: "relative",
        cursor: "pointer"
    },
    contentWrapper: {
        zIndex: "1",
        gridColumn: "5 / 10",
        gridRow: "1",
        padding: "10px",
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
    },
    lamboWrapper: {
        gridColumn: "1 / -1",
        gridRow: "1",
        [theme.breakpoints.down('sm')]:{
            display: "none",
        }
    },
    lambo: props => ({
        width: "100%",
        height: "auto",
        fill: props.color === "primary"?theme.palette.primary.main:theme.palette.secondary.main
    })
}));

export default function LamboButton(props){
    const classes = useStyles(props);
    const openUrl = () => {
        window.open(props.openUrl)
    }
    return (
        <Box className={classes.wrapper+" "+props.className} onClick={openUrl}>
            <div className={classes.contentWrapper}>
                {props.children}
            </div>
            <div className={classes.lamboWrapper}>
                <Lambo className={classes.lambo}/>
            </div>
        </Box>
    );
}
