import React, {createContext, useContext, useEffect, useState} from 'react';
import {useAlerts} from "./AlertProvider";
import Web3 from "web3";
import Web3Modal from "web3modal";
import axios from 'axios';
import {presaleContract} from "../contracts/presale";
import { InjectedConnector } from "@web3-react/injected-connector";
import { useWeb3React } from '@web3-react/core'

export const injected = new InjectedConnector({
    supportedChainIds: [ 1, 3,]
});


const WalletContext = createContext(null);

export const getGasPrices = async () => {
    const response = await axios.get("https://gasprice.poa.network/");
    if (response.data.health){
        return response.data
    }
    return false;
}

export default function WalletProvider(props) {
    const [provider, setProvider] = useState(null);
    const {alertError, alertInfo, alertSuccess} = useAlerts();
    const [walletAddress, setWalletAddress] = useState(null);
    const [txSpeed, setTxSpeed] = useState("standard");
    const [waiting, setWaiting] = useState([]);
    const [connecting, setConnecting] = useState(false);

    const connectWallet = () => {
        return injected.activate().then(async (connector) => {
                    const provider = {
                        web3: new Web3(connector.provider),
                        address: connector.account,
                    }
                    setWalletAddress(connector.account);
                    setProvider(provider);
                    alertSuccess(provider.address+" has been connected");
                    return provider;
                }).catch((error) => {
                    if (error.code === -32002){
                        alertInfo("Waiting for you to connect your wallet")
                    } else {
                        alertError("Your wallet is not connected");
                        throw error
                    }
                })
    }

    const handleAccounts = () => {
        setWalletAddress(null);
        connectWallet().catch(() => {
            alertError("Your wallet has been disconnected")
        });
    }

    useEffect(()=>{
        const {ethereum} = window;
        ethereum.on('accountsChanged', handleAccounts);
        ethereum.on('chainChanged', handleAccounts);
        ethereum.on('networkChanged', handleAccounts);
        ethereum.on('connect', handleAccounts);
        return () => {
            if (ethereum.removeListener) {
                ethereum.removeListener('accountsChanged', handleAccounts);
                ethereum.removeListener('chainChanged', handleAccounts);
                ethereum.removeListener('networkChanged', handleAccounts);
                ethereum.removeListener('connect', handleAccounts);
            }
        }
    }, [])

    const getProvider = () => {
        return new Promise((resolve, reject) =>{
            if (isConnected()){
                resolve(provider);
            } else if(connecting) {
                waiting.push(resolve);
            } else {
                connectWallet()
                    .then((provider)=>{
                        for (let waitingToResolve of waiting){
                            waitingToResolve(provider);
                        }
                        resolve(provider)
                    })
                    .catch((e) => reject(e));
            }
        });
    }

    const getGasPrice = async () => {
        const gasPrices = await getGasPrices();
        return Web3.utils.toWei(`${Math.ceil(gasPrices[txSpeed])}`, 'gwei');
    }

    const isConnected = () => {
        return !!provider;
    }

    const getOptions = async () => {
        const gasPrice = await getGasPrice();
        return {
            from: walletAddress,
            gasPrice: gasPrice
        }
    }

    const Contract = async (contract) => {
        const web3 = await getProvider();
        const web3Contract = new web3.eth.Contract(presaleContract.abi, presaleContract.address);
    }

    return (
        <WalletContext.Provider value={{
            provider,
            getProvider,
            getGasPrice,
            getGasPrices,
            isConnected,
            getOptions,
            walletAddress,
        }}>
            {props.children}
        </WalletContext.Provider>
    )
}

export const useWallet = () => {
    return useContext(WalletContext);
}
